.hamburger {
	background: $supared;
	position: fixed;
	top: 0px;
	right: 0;
	height: 36px;
	padding: 0;
	width: 40px;
	display: block;
	z-index: 99999999;
	transition: linear 0.5s all;
	opacity: 1;
	display: none;
	border-radius: 0 0 0 10px;
	@include r(720) {
		display: block; }
	&.is-active {
		height: 78px;
		width: 78px;
		border: 0;
		border-radius: 0; } }

.hamburger-box {
	width: auto;
	min-width: 30px; }

.hamburger-inner {
	background-color: #FFF;
	width: 100%;

	&:before {
		background-color: #FFF;
		width: 100%; }
	&:after {
		background-color: #FFF;
		width: 100%; } }

.toggle {
	z-index: -9999999;
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
	transition: ease-in 0.5s all;
	// transform: translateY(-100%)
	display: none;
	@include r(720) {
		display: block; }
	&.is-active {
		z-index: 9999999;
		opacity: 1;
		transition: ease-in 0.5s all;
		transform: translateY(0);
		background: $supared; }
	& .lang {
		width: 70px;
		height: 46px; }
	&__container {
		padding: 10px;
		height: 100%; }
	&__list {
		text-align: center;
		height: 100%;
		display: flex;
		flex-direction: column;
		-webkit-justify-content: space-around;
		justify-content: space-around; }
	&__item {
 }		// border: 1px solid #eee
	&__link {
		color: white;
		// font-family: Bebas
		letter-spacing: 2px;
		font-size: 22px;
		font-weight: 700;
		text-decoration: none;
		padding: 10px 0;
		&:hover {
			color: $supared;
			@include r(720) {
				color: #eee; } } } }

