.breadcrumbs {
	margin-bottom: 10px;
	&__list {
		display: flex; }
	&__item {
		margin-right: 10px;
		&:after {
			content: ' < ';
			font-size: 16px;
			font-weight: 600;
			padding: 5px;
			color: $supared; }
		&:last-child:after {
			content: none; }
		&:last-child {
			.breadcrumbs__link {
				color: $supared; } } }

	&__link {
		font-size: 16px;
		font-weight: 600;
		color: #000;
		text-decoration: none;
		@include h {
			text-decoration: underline; } } }

