$hero-height: 500px;
$hero-height1000px: 400px;
$hero-height720px: 350px;
$hero-height320px: auto;
$dark: #232528;

.hero {
	width: 100%;
	height: $hero-height;
	background-color: #eee;
	overflow: hidden;
	@include r(1000) {
		height: $hero-height1000px; }
	@include r(720) {
		height: $hero-height720px; }
	&__container {
		position: relative; } }

.nav {
	z-index: 999;
	width: 100%;
	position: absolute;
	top: 35px;
	transition: all 600ms ease;
	@include r(720) {
		display: none; }
	&__container {
		transition: all 400ms;
		max-width: 840px;
		width: 100%;
		margin: auto; }
	&__list {
		max-width: 840px;
		width: 100%;
		border-top: 3px solid $supared;
		margin: auto;
		display: flex;
		justify-content: space-between; }
	&__item {
		text-align: right;
		display: flex;
		width: 16.7%;
		height: 60px; }
	&__link {
		word-wrap: normal;
		width: 100%;
		max-width: 140px;
		min-width: 125px;
		padding: 5px;
		height: 60px;
		background-color: #eee;
		text-decoration: none;
		line-height: 76px;
		color: #000;
		font-size: 18px;
		font-weight: 600;
		transition: all 400ms;
		&:hover {
			color: $supared;
			height: 110px;
			line-height: 175px;
			transition: all 400ms; } }
	&__current {
		background-color: $supared;
		color: #eee;
		height: 110px;
		line-height: 175px;
		&:hover {
			color: #FFF; } }
	& .lang {
		display: none; } }


.nav-fixed {
	z-index: 999999;
	width: 100%;
	position: fixed;
	top: 0;
	@include r(720) {
		display: none; }
	&__container {
		box-shadow: 0 0 10px rgba(0,0,0,.6);
		border-top: 2px solid $supared;
		background: #eee;
		max-width: 100%;
		width: 100%;
		transition: all 400ms;
		margin: auto; }
	&__list {
		border: 0;
		max-width: 1000px;
		width: 100%;
		margin: auto;
		display: flex;
		justify-content: space-between; }
	&__item {
		text-align: right;
		display: flex;
		width: 16.666666%; }
	&__link {
		width: 100%;
		max-width: auto;
		min-width: 125px;
		padding: 5px;
		height: 30px;
		text-align: center;
		background-color: #eee;
		text-decoration: none;
		line-height: 20px;
		color: #000;
		font-size: 18px;
		font-weight: 600;
		transition: all 400ms;
		&:hover {
			color: $supared;
			box-shadow: inset 0px 0px 0px 1px $supared;
			transition: all 400ms ease; } }
	&__current {
		background-color: $supared;
		color: #eee;
		height: 30px;
		line-height: 20px;
		&:hover {
			color: #FFF; } }
	& .lang {
		width: 54px;
		height: 30px; } }


.carousel {
	position: relative;
	height: $hero-height;
	width: 100%;
	@include r(1000) {
		height: $hero-height1000px; }
	@include r(720) {
		height: $hero-height720px; }
	&__container {
		margin: 0;
		width: 100%;
		height: 100%; }
	&__item {
		z-index: -1;
		width: 100%;
		position: relative;
		&:after {
			content: "";
			@include coverdiv;
			background-color: rgba(0,0,0,.6);
			z-index: 1;
			@include r(720) {
				background-color: rgba(0,0,0,.7); } } }
	&__img {
		object-fit: cover;
		width: 100%;
		min-height: 400px;
		@include r(720); } }

.carousel-content {
	position: absolute;
	z-index: 2;
	width: 100%;
	display: none;
	@include r(720) {
		display: flex;
		flex-direction: column;
		align-content: space-around;
		justify-content: space-around;
		align-items: center;
		height: $hero-height720px; }
	&__logo {
		display: flex;
		-webkit-justify-content: space-around; }
	&__logo1 {
		background-image: url(../img/logo/logo1.png);
		width: 100px;
		height: 100px;
		border-radius: 100px;
		overflow: hidden;
		margin: 20px; }
	&__logo2 {
		background-image: url(../img/logo/logo2.png);
		width: 100px;
		height: 100px;
		border-radius: 100px;
		overflow: hidden;
		margin: 20px; }
	&__title {
		z-index: 2;
		width: 100%;
		text-align: center;
		vertical-align: center;
		font-size: 28px;
		font-weight: 600;
		font-weight: bold;
		padding: 10px;
		display: none;
		text-shadow: 3px 3px 1px #000000;
		@include r(720) {
			display: block; }
		@include r(480) {
			font-size: 20px; } }
	&__title-link {
		color: white;
		text-decoration: none;
		@include r(480) {
			font-size: 24px; }
		@include r(320) {
			font-size: 24px; } }
	&__title-span {
		color: $supared;
		font-size: 28px;
		@include r(720) {
			display: none;
			font-size: 24px; } } }

.arrow {
	position: absolute;
	font-family: "Cooper Black";
	cursor: pointer;
	z-index: 1;
	color: #eee;
	opacity: .8;
	font-size: 40px;
	width: 100px;
	top: 0;
	bottom: 0;
	background: rgba(288, 288, 288, 0);
	transition: all .5s ease;
	&:hover {
		text-shadow: #000 1px 1px 5px;
		background: rgba(288, 288, 288, 0.3);
		opacity: 1; }
	&__prev {
		left: 0; }
	&__next {
		right: 0; } }

.slick-dots {
	bottom: 40px;
	li {
		button {
			padding: 5px;
			&:before {
				height: 14px;
				width: 14px;
				background: #eee;
				transition: all 0.3s ease; }
			&:hover {
				&:before {
					background: $supared; } } }
		&.slick-active button:before {
				background: $supared; } } }







