.event__item:hover .event__date-month { color: #eee; }

.event__date { float: left; margin-right: 10px; margin-left: -10px; width: 75px; height: 75px; border-right: 2px solid rgba(140, 54, 63, 0.1); border-bottom: 2px solid rgba(140, 54, 63, 0.1); }

.event__date-day { color: #8c363f; text-align: center; font-size: 30px; font-weight: 600; line-height: 44px; }

.event__date-month { color: #8c363f; text-align: center; font-size: 20px; font-weight: 600; text-transform: uppercase; }

.event__name { font-size: 18px; font-weight: 600; margin-bottom: 10px; }

.event__name-link { color: #8c363f; text-decoration: none; }

.event__name-link:hover { text-decoration: underline; }

.event__description { font-size: 16px; font-weight: 400; line-height: 22px; text-align: justify; }

.event__visible { transition: all 400ms linear; opacity: 1; transform: translate(0px, 0px); }

.map { position: relative; margin-bottom: 10px; }

.map:hover .map__adress { background-color: #8c363f; }

.map__container { position: relative; width: 100%; height: 400px; background-color: #eee; }

.map__adress { position: absolute; width: 100%; bottom: 0; left: 0; padding: 20px; background-color: rgba(140, 54, 63, 0.1); color: white; font-size: 18px; text-align: center; }

.map__adress:hover { background-color: #8c363f; }

.map__zoom-in { background-image: url("../img/map/plus.svg"); }

.map__zoom-out { background-image: url("../img/map/minus.svg"); }

.map__zoom-in, .map__zoom-out { height: 50px; width: 50px; cursor: pointer; margin-left: 10px; margin-top: 10px; background-repeat: no-repeat; background-size: cover; opacity: 0.8; transition: all 0.25s ease; }

.map__zoom-in:hover, .map__zoom-out:hover { opacity: 1; }

.adress { font-size: 18px; padding: 3px; font-weight: 500; }

@media only screen and (max-width: 720px) { .adress { text-align: center; } }

.adress__link { text-decoration: none; color: #8c363f; font-weight: 600; }

.adress__link:hover { text-decoration: underline; }

.modal-mask { position: fixed; z-index: 9998; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.5); display: table; transition: opacity .3s ease; }

.modal-wrapper { display: table-cell; vertical-align: middle; }

.modal-container { width: 300px; margin: 0px auto; padding: 20px 30px; background-color: #fff; border-radius: 2px; box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33); transition: all .3s ease; font-family: Helvetica, Arial, sans-serif; }

.modal-header h3 { margin-top: 0; color: #42b983; }

.modal-body { margin: 20px 0; }

.modal-default-button { float: right; }

/* The following styles are auto-applied to elements with transition="modal" when their visibility is toggled by Vue.js. You can easily play with the modal transition by editing these styles. */
.modal-enter, .modal-leave-active { opacity: 0; }

.modal-enter .modal-container, .modal-leave-active .modal-container { -webkit-transform: scale(1.1); transform: scale(1.1); }

.breadcrumbs { margin-bottom: 10px; }

.breadcrumbs__list { display: flex; }

.breadcrumbs__item { margin-right: 10px; }

.breadcrumbs__item:after { content: ' < '; font-size: 16px; font-weight: 600; padding: 5px; color: #8c363f; }

.breadcrumbs__item:last-child:after { content: none; }

.breadcrumbs__item:last-child .breadcrumbs__link { color: #8c363f; }

.breadcrumbs__link { font-size: 16px; font-weight: 600; color: #000; text-decoration: none; }

.breadcrumbs__link:hover { text-decoration: underline; }

.hamburger { background: #8c363f; position: fixed; top: 0px; right: 0; height: 36px; padding: 0; width: 40px; display: block; z-index: 99999999; transition: linear 0.5s all; opacity: 1; display: none; border-radius: 0 0 0 10px; }

@media only screen and (max-width: 720px) { .hamburger { display: block; } }

.hamburger.is-active { height: 78px; width: 78px; border: 0; border-radius: 0; }

.hamburger-box { width: auto; min-width: 30px; }

.hamburger-inner { background-color: #FFF; width: 100%; }

.hamburger-inner:before { background-color: #FFF; width: 100%; }

.hamburger-inner:after { background-color: #FFF; width: 100%; }

.toggle { z-index: -9999999; position: fixed; width: 100%; height: 100%; top: 0; left: 0; opacity: 0; transition: ease-in 0.5s all; display: none; }

@media only screen and (max-width: 720px) { .toggle { display: block; } }

.toggle.is-active { z-index: 9999999; opacity: 1; transition: ease-in 0.5s all; transform: translateY(0); background: #8c363f; }

.toggle .lang { width: 70px; height: 46px; }

.toggle__container { padding: 10px; height: 100%; }

.toggle__list { text-align: center; height: 100%; display: flex; flex-direction: column; -webkit-justify-content: space-around; justify-content: space-around; }

.toggle__link { color: white; letter-spacing: 2px; font-size: 22px; font-weight: 700; text-decoration: none; padding: 10px 0; }

.toggle__link:hover { color: #8c363f; }

@media only screen and (max-width: 720px) { .toggle__link:hover { color: #eee; } }
